.home {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
}

.home_section {
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
}

.home_row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}

.home_image {
    width: 100%;
    z-index: -1;
    margin-bottom: -240px;    
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1) 0, rgb(104, 103, 103) 50%, rgba(0, 0, 0, 0) 100%);
}
